const requireApi = require.context(
	// api
	'.',
	// 
	false,
	// 
	/.js$/
)

let module = {}

requireApi.keys().forEach(item => {
	if (item === './index.js') return
	//  requireApi(item) push  module 
	Object.assign(module, requireApi(item))
})

export default module